        box-shadow: 10014px -15px 0 0 rgba(152, 128, 255, 0);
    }
    25%,
    50%,
    75% {
        box-shadow: 10014px 0 0 0 #9880ff;
    }
    100% {
        box-shadow: 10014px 15px 0 0 rgba(152, 128, 255, 0);
    }
}


/**
 * ==============================================
 * Dot Stretching
 * ==============================================
 */

.dot-stretching {
    position: relative;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #9880ff;
    color: #9880ff;
    transform: scale(1.25, 1.25);
    animation: dotStretching 2s infinite ease-in;
}

.dot-stretching::before,
.dot-stretching::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
}

.dot-stretching::before {
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #9880ff;
    color: #9880ff;
    animation: dotStretchingBefore 2s infinite ease-in;
}

.dot-stretching::after {
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: #9880ff;
    color: #9880ff;
    animation: dotStretchingAfter 2s infinite ease-in;
}

@keyframes dotStretching {
    0% {
        transform: scale(1.25, 1.25);
    }
    50%,
    60% {
        transform: scale(0.8, 0.8);
    }
    100% {
        transform: scale(1.25, 1.25);
    }
}

@keyframes dotStretchingBefore {
    0% {
        transform: translate(0) scale(0.7, 0.7);
    }
    50%,
    60% {
        transform: translate(-20px) scale(1, 1);
    }
    100% {
        transform: translate(0) scale(0.7, 0.7);
    }
}

@keyframes dotStretchingAfter {
    0% {
        transform: translate(0) scale(0.7, 0.7);
    }
    50%,
    60% {
        transform: translate(20px) scale(1, 1);
    }
    100% {
        transform: translate(0) scale(0.7, 0.7);
    }
}


/**
 * ==============================================
 * Experiment-Gooey Effect
 * Dot Gathering
 * ==============================================
 */

.dot-gathering {
    position: relative;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: black;
    color: transparent;
    margin: -1px 0;
    filter: blur(2px);
}

.dot-gathering::before,
.dot-gathering::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: -50px;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: black;
    color: transparent;
    opacity: 0;
    filter: blur(2px);
    animation: dotGathering 2s infinite ease-in;
}

.dot-gathering::after {
    animation-delay: .5s;
}

@keyframes dotGathering {
    0% {
        opacity: 0;
        transform: translateX(0);
    }
    35%,
    60% {
        opacity: 1;
        transform: translateX(50px);
    }
    100% {
        opacity: 0;
        transform: translateX(100px);
    }
}


/**
 * ==============================================
 * Experiment-Gooey Effect
 * Dot Hourglass
 * ==============================================
 */

.dot-hourglass {
    position: relative;
    top: -15px;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: black;
    color: transparent;
    margin: -1px 0;
    filter: blur(2px);
    transform-origin: 5px 20px;
    animation: dotHourglass 2.4s infinite ease-in-out;
    animation-delay: .6s;
}

.dot-hourglass::before,
.dot-hourglass::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: black;
    color: transparent;
    filter: blur(2px);
}

.dot-hourglass::before {
    top: 30px;
}

.dot-hourglass::after {
    animation: dotHourglassAfter 2.4s infinite cubic-bezier(0.65, 0.05, 0.36, 1);
}

@keyframes dotHourglass {
    0% {
        transform: rotateZ(0deg);
    }
    25% {
        transform: rotateZ(180deg);
    }
    50% {
        transform: rotateZ(180deg);
    }
    75% {
        transform: rotateZ(360deg);
    }
    100% {
        transform: rotateZ(360deg);
    }
}

@keyframes dotHourglassAfter {
    0% {
        transform: translateY(0);
    }
    25% {
        transform: translateY(30px);
    }
    50% {
        transform: translateY(30px);
    }
    75% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(0);
    }
}


/**
 * ==============================================
 * Experiment-Gooey Effect
 * Dot Overtaking
 * ==============================================
 */

.dot-overtaking {
    position: relative;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: transparent;
    color: black;
    margin: -1px 0;
    box-shadow: 0 -20px 0 0;
    filter: blur(2px);
    animation: dotOvertaking 2s infinite cubic-bezier(0.2, 0.6, 0.8, 0.2);
}

.dot-overtaking::before,
.dot-overtaking::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: transparent;
    color: black;
    box-shadow: 0 -20px 0 0;
    filter: blur(2px);
}

.dot-overtaking::before {
    animation: dotOvertaking 2s infinite cubic-bezier(0.2, 0.6, 0.8, 0.2);
    animation-delay: .3s;
}

.dot-overtaking::after {
    animation: dotOvertaking 1.5s infinite cubic-bezier(0.2, 0.6, 0.8, 0.2);
    animation-delay: .6s;
}

@keyframes dotOvertaking {
    0% {
        transform: rotateZ(0deg);
    }
    100% {
        transform: rotateZ(360deg);
    }
}


/**
 * ==============================================
 * Experiment-Gooey Effect
 * Dot Shuttle
 * ==============================================
 */

.dot-shuttle {
    position: relative;
    left: -15px;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: black;
    color: transparent;
    margin: -1px 0;
    filter: blur(2px);
}

.dot-shuttle::before,
.dot-shuttle::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: black;
    color: transparent;
    filter: blur(2px);
}

.dot-shuttle::before {
    left: 15px;
    animation: dotShuttle 2s infinite ease-out;
}

.dot-shuttle::after {
    left: 30px;
}

@keyframes dotShuttle {
    0%,
    50%,
    100% {
        transform: translateX(0);
    }
    25% {
        transform: translateX(-45px);
    }
    75% {
        transform: translateX(45px);
    }
}